/* Header */

header {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 768px) {
        margin-top: 20px;
        margin-right: 20px;
        margin-left: 20px;
    }

    @media screen and (min-width:768px) {
        margin-top: 35px;
        margin-right: 35px;
        margin-left: 35px;
    }

    @media screen and (min-width: 1020px) {
        margin-top: 45px;
        margin-right: 100px;
        margin-left: 100px;
        margin-bottom: 50px;
    }

    img {
        @media screen and (max-width: 768px) {
            width: 145px;
            height: 46px;
        }

        @media screen and (min-width:768px) {
            width: 180px;
            height: 53px;
        }

        @media screen and (min-width: 1020px) {
            width: 210px;
            height: 68px;
        }
    }

    .navbar {
        display: flex;

        ul {
            display: flex;
            flex-direction: row;
            align-items: center;

            @media screen and (max-width: 768px) {
                gap: 25px;
            }

            @media screen and (min-width: 768px) {
                gap: 57px;
            }

            a {
                text-decoration: none;
                color: black;

                &.link-underline {
                    text-decoration: underline;
                }
            }
        }

        li {
            list-style-type: none;

            @media screen and (max-width: 768px) {
                text-transform: uppercase;
                font-size: 12px;
                font-weight: 500;
            }

            @media screen and (min-width: 768px) {
                font-size: 24px;
            }
        }
    }
}