/* Banner */

.banner {
    display: flex;
    justify-content: center;
    position: relative;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    background: #000;
    mix-blend-mode: darken;

    @media screen and (min-width:768px) {
        margin-right: 35px;
        margin-left: 35px;
    }

    @media screen and (min-width: 1020px) {
        margin-left: 100px;
        margin-right: 100px;
    }

    .img-home {
        opacity: 0.3;
    }

    .img-about {
        opacity: 0.7;
    }

    img {
        width: 100%;
        height: 111px;
        border-radius: 10px;
        object-fit: cover;

        @media screen and (min-width: 768px) {
            height: 223px;
        }
    }

    h1 {
        position: absolute;
        display: flex;
        align-items: center;
        padding-left: 20px;
        padding-right: 20px;
        height: 70%;
        font-size: 24px;
        font-weight: 700;
        color: #fff;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

        @media screen and (min-width: 768px) {
            height: 85%;
        }
    }
}